import React, { Component } from "react";
import { connect } from "react-redux";
import actions from "../duck/actions";

import Table from "./Table.js";
import UnderTable from "./UnderTable.js";

class Form extends Component {
  state = {
    dateFrom: "",
    dateTo: "",
    Monday: {},
    Tuesday: {},
    Wednesday: {},
    Thursday: {},
    Friday: {},
    Saturday: {},
    Sunday: {},
    outside: {}
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  getWeek = schedule => {
    this.setState({
      Monday: schedule.Monday,
      Tuesday: schedule.Tuesday,
      Wednesday: schedule.Wednesday,
      Thursday: schedule.Thursday,
      Friday: schedule.Friday,
      Saturday: schedule.Saturday,
      Sunday: schedule.Sunday
    });
  };

  handleSubmit = () => {
    let id = this.props.counter;
    id++;
    const {
      dateFrom,
      dateTo,
      Monday,
      Tuesday,
      Wednesday,
      Thursday,
      Friday,
      Saturday,
      Sunday,
      outside
    } = this.state;
    const { add } = this.props;
    const week = {
      id,
      dateFrom,
      dateTo,
      Monday,
      Tuesday,
      Wednesday,
      Thursday,
      Friday,
      Saturday,
      Sunday,
      outside
    };
    add(week);
    console.log(week);
  };

  render() {
    return (
      <div className="schedule">
        <span>Harmonogram od:</span>
        <input
          name="dateFrom"
          onChange={this.handleChange}
          type="date"
          required
        />
        <span>do:</span>
        <input
          name="dateTo"
          onChange={this.handleChange}
          type="date"
          required
        />
        <Table getWeek={this.getWeek} />
        <UnderTable />
        <button onClick={this.handleSubmit}>Zapisz</button>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  counter: state.schedules.counter
});

const mapDispatchToProps = dispatch => ({
  add: newSchedule => dispatch(actions.add(newSchedule))
});

export default connect(mapStateToProps, mapDispatchToProps)(Form);
