import React, { Component } from "react";

import Multitude from "./Multitude.js";

export default class Shift extends Component {
  state = {
    multitude1: [],
    multitude2: []
  };

  getMultitude = (name, multitude) => {
    this.setState({
      [name]: multitude
    });

    const { shiftName, getShift } = this.props;
    const shiftValue = this.state;
    getShift(shiftName, shiftValue);
  };

  render() {
    const { shiftName, time } = this.props;
    return (
      <div className={shiftName}>
        <p>{`${time}`}</p>
        <Multitude
          getMultitude={this.getMultitude}
          multitudeName={"multitude1"}
        />
        <Multitude
          getMultitude={this.getMultitude}
          multitudeName={"multitude2"}
        />
      </div>
    );
  }
}
